import React, { useState, useEffect } from "react";
import classNames from "classnames";
import styles from "./ReactSheet.module.css";

function ReactSheetDialog(props) {
  // Declare state variable for sheet/dialog open state
  const [open, setOpen] = useState(false);
  // Destructure the modal and dialogType props
  const { modal, dialogType } = props;

  // Function to open the sheet/dialog
  const openSheet = () => {
    setOpen(true);
  };

  // Function to close the sheet/dialog
  const closeSheet = () => {
    setOpen(false);
  };

  // Function to handle the Esc key press event
  const handleEscKey = (event) => {
    if (event.key === "Escape" && open && modal) {
      closeSheet();
    }
  };

  const isStandardDialog = dialogType === "standard";
  // Determine the sheet class based on the open state
  const sheetOpenClass = open ? styles.open : "";
  // Determine if the overlay should be displayed
  const overlayClass = open && modal;
  // Combine sheet classes based on the dialog type
  const sheetStyle = classNames(
    styles.sheet,
    sheetOpenClass,
    {
      [styles.standardDialog]: isStandardDialog,
    }
  );

  // Add event listener for keydown events to handle Esc key press
  useEffect(() => {
    window.addEventListener("keydown", handleEscKey);
    return () => {
      window.removeEventListener("keydown", handleEscKey);
    };
  }, [handleEscKey]);

  // Set focus on the sheet heading when the sheet/dialog opens
  useEffect(() => {
    if (open) {
      document.getElementById("sheet-heading").focus();
    }
  }, [open]);

  // Add an effect to return focus to the triggering button when sheet/dialog is closed
  useEffect(() => {
    if (!open) {
      const sideSheetButton = document.getElementById("side-sheet-button");
      if (sideSheetButton) {
        sideSheetButton.focus();
      }
    }
  }, [open]);

  // Function to trap focus within the sheet/dialog when modal
  const trapFocus = (event) => {
    if (!open || !modal) {
      return;
    }
    const isTabPressed = event.key === "Tab" || event.keyCode === 9;

    if (!isTabPressed) {
      return;
    }

    const trapEl =
      'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])';
    const trapContent = document.querySelectorAll(trapEl);
    const firstTrapEl = trapContent[0];
    const lastTrapEl = trapContent[trapContent.length - 1];

    if (event.shiftKey) {
      if (document.activeElement === firstTrapEl) {
        lastTrapEl.focus();
        event.preventDefault();
      }
    } else {
      if (document.activeElement === lastTrapEl) {
        firstTrapEl.focus();
        event.preventDefault();
      }
    }

    if (!document.getElementById("sheet-heading").contains(event.target)) {
      event.stopPropagation();
      document.getElementById("sheet-heading").focus();
    }
  };

  return (
    <>
      <button
        onClick={openSheet}
        id="side-sheet-button"
        className={styles.sideSheetButton}
      >
        Open Sheet
      </button>
      {overlayClass && <div className={styles.overlay}></div>}
      <div
        className={sheetStyle}
        role="dialog"
        aria-labelledby="sheet-heading"
        {...(modal ? { "aria-modal": true } : {})}
        aria-hidden={!open}
        onKeyDown={(event) => {
          trapFocus(event);
        }}
      >
        <div className={styles.sheetHeader}>
          <h2 id="sheet-heading" className={styles.sheetHeading} tabIndex="-1">
            Sheet Dialog
          </h2>
          <button className={styles.closeButton} onClick={closeSheet}>
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M6 18L18 6M6 6L18 18"
                stroke="#7B61C4"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>
          </button>
        </div>
        <div className={styles.sheetContent}>
          <h3>Control Props</h3>
          <p>
            This component has two control props: <code>modal</code> and{" "}
            <code>dialogType</code>.
          </p>
          <ul>
            <li>
              <code>modal</code>: Determines if the sheet/dialog
            </li>
            <li>
              <code>dialogType</code>: Defines the type of dialog to display,
              either "sheet" or "standard". It accepts a string value (default:
              "sheet").
            </li>
          </ul>
          <h3>When to Use Sheet and Standard Dialogs</h3>
          <p>
            Both sheet and standard dialogs can be used to display additional
            content, prompt user input, or present options in a separate layer.
            However, they serve different purposes based on the context and user
            experience you want to achieve.
          </p>
          <h4>Sheet Dialog</h4>
          <p>
            Sheet dialogs are ideal for displaying content or actions related to
            the current context or page. They usually slide in from the edge of
            the screen and cover only a portion of the content, allowing the
            user to maintain a connection with the underlying page. Sheet
            dialogs are typically used for navigation menus, filters, or
            contextual actions on larger screens.
          </p>
          <h4>Standard Dialog</h4>
          <p>
            Standard dialogs are used to display more critical information,
            prompt user input, or present actions that require the user's full
            attention. They usually appear in the center of the screen, with a
            backdrop that prevents interaction with the content underneath. Use
            standard dialogs for confirmation dialogs, user input forms, or when
            the user must complete an action before proceeding.
          </p>
        </div>
      </div>
    </>
  );
}

export default ReactSheetDialog;
